
import Blur from 'vux/src/components/blur/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV1Data from 'vux/src/datas/china_address_v1.json'

import axios from 'axios'

export default {
    components: {
        Blur,
        Cell,
        Group,
        Selector,
        XAddress
    },
    data() {
        return {
            items: [
                {
                    key: 0,
                    value: '男'
                },
                {
                    key: 1,
                    value: '女'
                }
            ],
            sex: this.$store.state.userInfo.sex,
            addressData: ChinaAddressV1Data,
            address: this.$store.state.userInfo.address
        }
    },
    methods: {
        changeAddress(str) {
            if (str) {
                axios.defaults.headers.post['x-access-token'] = window.localStorage.token
                axios.post('/profile',{
                    address: this.address
                }).then(() => {

                }, () => {
                    this.$vux.toast.show({
                        text: '修改失败',
                        type: 'warn'
                    })
                })
            }
        },
        changeSex(str) {
            axios.defaults.headers.post['x-access-token'] = window.localStorage.token
            axios.post('/profile',{
                sex: str
            }).then(() => {

            },() => {
                this.$vux.toast.show({
                    text: '修改失败',
                    type: 'warn'
                })
            })
        }
    }
}



